import React from "react"
import { MDBBox, MDBContainer } from "mdbreact";

const Footer = () => (
  <>
    <MDBBox tag="footer" mt={5}>
      <MDBContainer className="text-center">
        <hr className="my-4" />
        <p> {new Date().getFullYear()}</p>
        <p> &laquo; &raquo;</p>
      </MDBContainer>
    </MDBBox>
  </>
)

export default Footer
