import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import { MDBContainer, MDBTypography } from "mdbreact";
import "./header.css"

const Header = () => {
  const data = useStaticQuery(graphql`
  query {
    logoImg: file(relativePath: { eq: "yaklass_logo_transparent.png" }) {
      childImageSharp {
        fluid(maxWidth: 300) {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`)

  return (
    <header id="header">
      <div className="header-logo">
        <MDBContainer>
          <Img fluid={data.logoImg.childImageSharp.fluid} className="header-logo__img" />
        </MDBContainer>
      </div>
      <MDBContainer className="d-flex flex-column justify-content-center text-center header-content">
        <MDBTypography tag="h1" variant="h1-responsive" className="text-center">
          <span className="text-nowrap"></span> !
        </MDBTypography>
        <MDBTypography tag="h2" variant="h2-responsive" className="text-center">
            <span className="text-nowrap"></span> !
        </MDBTypography>
      </MDBContainer>
    </header >
  )
}

export default Header
